//?	Core (WPF2 Versão 1.5.0)
//!	Estruturação de páginas e disposição de elementos:

//* Instâncias: extensões da folha de estilos.
@use "vars"		 		as var;
@use "mix" 				as mix;
@use "fun"				as fun;
@use "sass:color"		as color;
@use "sass:math"		as math;

//* Main (Altere como precisar):
*{
	&::selection{
		background: var.$c-t1-800;
		color: var.$c-s-1;
	}

	::-webkit-scrollbar-track {
		background: var.$c-bg-2; 
	}

	::-webkit-scrollbar-thumb {
		background: var.$c-t1-800;

		&:hover{
			background: var.$c-t1-900; 
		} 
	}
	
	&::-webkit-scrollbar {
		width: 5px;
		height: 5px;
	}
}

body{
	background-color: var.$c-surf-1;
	font-family: var.$fnt-1;
	font-weight: var.$wgt-regular;
	// cursor: var.$mc-auto;

	position: relative;
	overflow-x: hidden;

	&.inactive{
		overflow: none;

		&:after{
			@include mix.setAbsolute(0, 0);	
			
			content: '';
			background: rgba(0, 0, 0, 0.5);
			width: 100%;
			height: 100%;
			z-index: 9;
		}
	}

	@include mix.responsive()
	{
		.mob\:inactive{
			overflow: none;

			&:after{
				@include mix.setAbsolute(0, 0);	
				
				content: '';
				background: rgba(0, 0, 0, 0.5);
				width: 100%;
				height: 100%;
				z-index: 9;
			}
		}
	}
}

//* Themes: (Altere como precisar)
main{
	display: block;
	width: 100%;
}

.theme{
	&-1{
		background-color: var.$c-bg-1;
	}
	
	&-2{
		// background-color: var.$c-bg-2;
	}
	
	&-3{
		// background-color: var.$c-bg-3;
	}
}

@include mix.responsive()
{
	.mob\:theme{
		&-1{
			background-color: var.$c-bg-1;
		}
		
		&-2{
			// background-color: var.$c-bg-2;
		}
		
		&-3{
			// background-color: var.$c-bg-3;
		}
	}
}

//* Areas (Altere como precisar):
.max-area{
	max-width: var.$pg-maxwidth;
	
	display: flex;
	justify-content: center;
	flex-direction: column;
	margin: 0 auto;

	@include mix.responsive(){
		width: 100%;
	}
}

//! NÃO ALTERE NADA A PARTIR DESTE PONTO A MENOS QUE SAIBA O QUE ESTÁ FAZENDO!
.hidden{
	display: none!important;
}

@include mix.responsive()
{
	.mob\:hidden{
		display: none!important;
	}
}

//* Cursores:
.mc{
	&-auto{
		cursor: auto;
	}

	&-pointer{
		cursor: pointer;
	}

	&-help{
		cursor: context-menu;
	}

	&-grab{
		cursor: grab;
	}

	&-grabbed{
		cursor: grabbing;
	}

	&-text{
		cursor: text;
	}

	&-zoom-in{
		cursor: zoom-in;
	}

	&-zoom-out{
		cursor: zoom-out;
	}

	&-resize{
		cursor: nwse-resize;
	}
}

//* Positioning:
.glued, .overflow-hidden{
	overflow: hidden!important;
}

.relative{
	position: relative!important;
}

.absolute{
	position: absolute!important;
}

.fixed{
	position: fixed!important;
}

@include mix.responsive()
{
	.mob\:{
		&glued, 
		&overflow-hidden{
			overflow: hidden!important;
		}

		&relative{
			position: relative!important;
		}

		&absolute{
			position: absolute!important;
		}

		&fixed{
			position: fixed!important;
		}
	}
}

//* Text Orientation:
.t-left			{text-align: left}
.t-center		{text-align: center}
.t-right		{text-align: right}
.t-justify		{text-align: justify}

@include mix.responsive()
{
	.mob\:{
		&t-left			{text-align: left}
		&t-center		{text-align: center}
		&t-right		{text-align: right}
		&t-justify		{text-align: justify}
	}
}

//* Padding:
.p{
	//? General
	@for $p from 1 through 5{
		&-#{$p}{
			padding: #{$p * 1%};
		}
	}

	@for $p from 1 through 300{
		&-#{$p}px{
			padding: #{$p * 1px};
		}
	}

	@for $p from 1 through 30{
		&-#{$p}r{
			padding: #{$p * 1rem};
		}
	}

	&-a, &-auto{
		padding: auto;
	}

	&-0{
		padding: 0!important;
	}
	
	//? Y-Axis
	&y{
		@for $py from 1 through 5{
			&-#{$py}{
				padding-top: #{$py * 1%};
				padding-bottom: #{$py * 1%};
			}
		}
		
		@for $py from 1 through 300{
			&-#{$py}px{
				padding-top: #{$py * 1px};
				padding-bottom: #{$py * 1px};
			}
		}
		
		@for $py from 1 through 15{
			&-#{$py}r{
				padding-top: #{$py * 1rem};
				padding-bottom: #{$py * 1rem};
			}
		}

		&-a, &-auto{
			padding-top: auto;
			padding-bottom: auto;
		}	
		
		&-0{
			padding-top: 0;
			padding-bottom: 0;
		}
	}
	
